import React, { useContext } from 'react'
import { Box } from '@chakra-ui/react'
import styled, { keyframes } from 'styled-components'
import DisplayTable from './component/DisplayTable'
import { StyledButtonBox } from 'utils/StyledButton'
import { purchaseContext } from './useContext/createContext'

const fadeInRight = keyframes`
  0% {
    transform:translate(100%)
  }
  100% {
	transform:translate(0%)
  }
`

const StyledTransitionBg = styled(Box)`
	width: 100%;
	animation: ${fadeInRight} 0.5s ease;

	.info {
		ul {
			list-style: none;
			display: flex;
		}
	}
	@media print {
		& {
			position: absolute;
			${'' /* width: 100vw !important; */}
			border: 1px solid red;
			top: 0;
			right: 0;
			left: 0;
			background: white;
			height: 100vh;

			.success-button {
				display: none !important;
			}

			.precautions {
				display: block;
				padding-bottom: 3em !important;

				ul {
					list-style: none;
				}
			}
		}
	}
`

export default function DisplayContent() {
	const { setEditState } = useContext(purchaseContext)
	return (
		<StyledTransitionBg>
			<Box textAlign="center">
				<Box className="title" fontSize="2em" mb=".7em">
					OPMM
				</Box>
			</Box>
			<Box className="info" mb="1em">
				<Box d="flex" w="600px" justifyContent="space-between" mx="auto">
					<Box>
						<Box>1100101-001</Box>
						<Box>110/01/11</Box>
					</Box>
					<Box>
						<Box></Box>
						<Box>0912345678</Box>
					</Box>
					<Box>
						<Box></Box>
						<Box>0987654321</Box>
					</Box>
				</Box>
			</Box>
			<DisplayTable />
			<Box textAlign="center">
				<Box textAlign="right" my="1em" mr="1em">
					$1,200
				</Box>
				<Box d="inline-block" className="success-button">
					<StyledButtonBox
						onClick={() => setEditState((pre) => (pre = { ...pre, step: 0 }))}
					>
						
					</StyledButtonBox>
				</Box>
			</Box>
			<Box
				className="precautions"
				borderTop="1px solid"
				borderColor="gray.600"
				px="3em"
				display="none"
			>
				<Box fontSize="2em" textAlign="center" mt="1em" mb="2em">
					
				</Box>
				<Box fontSize="1.5em">
					<ul>
						<li> QR code, </li>
						<li>
							 
							0912345678
						</li>
						<li> , QR code</li>
						<li>
							 , 0912345678,
							1
						</li>
					</ul>
				</Box>
			</Box>
		</StyledTransitionBg>
	)
}
